import React, { useState } from 'react';
import axios from 'axios';

const UploadImage = () => {
  const [file, setFile] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [success, setSuccess] = useState(null);

  const handleFileChange = e => {
    setFile(e.target.files[0]);
  };

  const handleSubmit = async e => {
    e.preventDefault();
    setLoading(true);

    try {
      const formData = new FormData();
      formData.append('file', new Blob([file]), file.name);
      formData.append('title', 'Your Media Title');
      formData.append('caption', 'Your Media Caption');
      formData.append('alt_text', 'Your Alt Text');
      formData.append('description', 'Your Media Description');

      const response = await axios.post(
        'http://imagecropper.000webhostapp.com/wp-json/wp/v2/media',
        formData,
        {
          headers: {
            'Content-Type': 'multipart/form-data',
            Authorization: 'Basic ' + btoa('admin:Hello@123')
          }
        }
      );

      setSuccess(response.data);
    } catch (error) {
      setError(error);
    }

    setLoading(false);
  };

  return (
    <form onSubmit={handleSubmit}>
      <input type="file" onChange={handleFileChange} />
      <button type="submit">Upload Image</button>
      {loading && <p>Uploading...</p>}
      {error && <p>Error: {error.message}</p>}
      {success && <p>Success: {success.id}</p>}
    </form>
  );
};

export default UploadImage;
